.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-default-black, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans-modal,
    transform $trans-modal,
    visibility $trans-modal;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;
    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 22px 56px 44px 40px;

    @include vp-767 {
      padding: 132px 16px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;
    width: 500px;
    padding: 40px;
    border-radius: 3px;
    background-color: $color-deep-sky-blue;

    @include vp-767 {
      width: 100%;
      padding: 20px;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    row-gap: 20px;

    input[type="text"],
    input[type="tel"],
    textarea {
      width: 100%;
      padding: 15px;
      margin: 0;
      border: none;
      border-radius: 3px;
      background-color: $color-black-pearl-second-opacity;
      font-family: $ff-pt-mono;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      color: $color-alice-blue;

      &:hover,
      &:focus {
        opacity: 0.7;
      }

      &::placeholder {
        color: $color-default-white;
      }
    }

    button {
      grid-area: button;
      padding: 13px 25px;
      background-color: $color-black-pearl;
      color: $color-default-white;
      font-weight: 700;
      font-size: 16px;
      line-height: 19px;
      text-align: center;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      border-radius: 3px;
      // min-width: 245px;
      border: none;

      @media (max-width: 1023px) {
        padding-top: 15px;
      }
  }

  &__title {
    font-family: $ff-roboto-condensed;
    font-weight: 400;
    font-size: 30px;
    line-height: 42px;
    color: $color-default-white;
    margin: 0;
    margin-bottom: 8px;
  }

  &__description {
    font-family:$ff-pt-mono;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: $color-default-white;
    margin: 0;
    margin-bottom: 20px;
  }



  &__close-btn {  // !!!нет цвета Х //
    position: absolute;
    top: 15px;
    right: 15px;
    z-index: 1;
    width: 40px;
    height: 40px;
    padding: 0;
    background: $color-transparent;
    fill: $color-default-white;
    border: none;
    cursor: pointer;
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include vp-767 {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    transition: none;
  }

  &.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;

    pointer-events: auto;
  }
  }
}
